import delay from './delay';


const opearators = [
  {
    id: 'mts',
    name: ''
  },
  {
    id: 'beeline',
    name: ''
  },
  {
    id: 'megafon',
    name: ''
  }
];

class OperatorApi {
  static getAllOperators() {
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve([...opearators]);
      }, delay);
    });
  }
}

export default OperatorApi;
